@import "../utils/vars";
@import "../utils/mixins";
@import "../utils/extends";

.page {
  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateY(-100%);

    transition: transform $transitionDuration;

    &:target {
      transform: translate(0);
    }
  }
}
